<template>
  <div style="position: relative">
    <div class="search-container">
      <h1 class="heading">IP Address Tracker</h1>
      <div class="search-input">
        <input
          type="text"
          placeholder="Search for any IP address or domain"
          class="search-bar"
          v-model="searchText"
          @keydown.enter="search"
        />
        <button class="search-btn" @click="search">
          <img src="../assets/icon-arrow.svg" alt="icon-arrow" />
        </button>
      </div>
    </div>
    <info-card v-bind="$props"></info-card>
  </div>
</template>

<script>
import InfoCard from "./InfoCard.vue";

export default {
  name: "Search",
  props: {
    infoObj: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      searchText: "",
    };
  },
  components: { InfoCard },
  methods: {
    search() {
      if (this.searchText) {
        this.$emit("searchEvent", this.searchText);
      }
    },
  },
};
</script>
