<template>
  <div class="info-card" v-if="Object.keys(infoObj).length">
    <div>
      <h2 class="info-heading">IP ADDRESS</h2>
      <p class="info">{{ infoObj.ip }}</p>
    </div>
    <div>
      <h2 class="info-heading">LOCATION</h2>
      <p class="info">
        {{ infoObj.location.city }}, {{ infoObj.location.region }},
        {{ infoObj.location.postalCode }}
      </p>
    </div>
    <div>
      <h2 class="info-heading">TIMEZONE</h2>
      <p class="info">UTC {{ infoObj.location.timezone }}</p>
    </div>
    <div>
      <h2 class="info-heading">ISP</h2>
      <p class="info">{{ infoObj.isp }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "InfoCard",
  props: {
    infoObj: {
      type: Object,
      required: true,
    },
  },
};
</script>
