<template>
  <div id="map"></div>
</template>

<script>
import leaflet from "leaflet";
import "leaflet/dist/leaflet.css";
delete leaflet.Icon.Default.prototype._getIconUrl;

leaflet.Icon.Default.mergeOptions({
  iconRetinaUrl: require("../assets/icon-location.svg"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
  iconSize: ["auto", "auto"],
});

let map;
export default {
  name: "Map",
  props: {
    infoObj: {
      type: Object,
      required: true,
    },
    updateMap: {
      type: Boolean,
      required: true,
    },
  },
  watch: {
    updateMap(newVal) {
      if (newVal) {
        leaflet
          .marker([this.infoObj.location.lat, this.infoObj.location.lng])
          .addTo(map);
        map.setView([this.infoObj.location.lat, this.infoObj.location.lng], 15);
      }
    },
  },
  mounted() {
    map = leaflet.map("map");
    leaflet
      .tileLayer(
        "https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoiZ3VydmluNzciLCJhIjoiY2wyNTAxejlnMjR5ZTNpbHA2MHE4OGpvdSJ9.zZdU2xaLmI0Puf2AJpVnkg",
        {
          maxZoom: 18,
          id: "mapbox/streets-v11",
          tileSize: 512,
          zoomOffset: -1,
          accessToken:
            "pk.eyJ1IjoiZ3VydmluNzciLCJhIjoiY2wyNTAxejlnMjR5ZTNpbHA2MHE4OGpvdSJ9.zZdU2xaLmI0Puf2AJpVnkg",
        }
      )
      .addTo(map);
  },
};
</script>
