<template>
  <div>
    <search @searchEvent="search" :infoObj="infoObj" />
    <Map :infoObj="infoObj" :update-map="updateMap" />
  </div>
</template>

<script>
import Search from "./components/Search.vue";
import Map from "./components/Map.vue";
import axios from "axios";

export default {
  name: "App",
  components: {
    Search,
    Map,
  },
  data() {
    return {
      infoObj: {},
      updateMap: false,
    };
  },
  methods: {
    search(searchText) {
      this.updateMap = false;
      axios(
        `https://geo.ipify.org/api/v2/country,city?apiKey=at_B6opCe2wncX1sumkOMbwjqlmj6wVi&ipAddress=${searchText}`
      )
        .then((response) => {
          this.infoObj = response.data;
          this.updateMap = true;
        })
        .catch(() => {
          alert(
            "There was an error while fetching data from server. Please check the IP address and try again."
          );
          this.updateMap = false;
        });
    },
  },
  mounted() {
    this.search("");
  },
};
</script>
